import { BlogPost } from './blogService';

interface XMLBlogPost {
  id: string;
  title: string;
  content: string;
  date: string;
  imageUrl: string;
  category: string;
  author: string;
  excerpt: string;
}

export const parseXMLBlogPosts = (xmlString: string): BlogPost[] => {
  const parser = new DOMParser();
  const xmlDoc = parser.parseFromString(xmlString, 'text/xml');
  const posts: BlogPost[] = [];

  // Get all item elements from RSS feed
  const postElements = xmlDoc.getElementsByTagName('item');

  for (let i = 0; i < postElements.length; i++) {
    const post = postElements[i];
    
    // Extract data from RSS feed
    const postData: XMLBlogPost = {
      id: getElementText(post, 'guid') || '',
      title: getElementText(post, 'title') || '',
      content: getElementText(post, 'description') || '',
      date: getElementText(post, 'pubDate') || '',
      imageUrl: '', // RSS doesn't have a standard image field
      category: getElementText(post, 'category') || '',
      author: 'Faclair nan Gèidheal Team', // RSS doesn't have a standard author field
      excerpt: getElementText(post, 'description') || '',
    };

    // Validate required fields
    if (!postData.id || !postData.title || !postData.content || !postData.date) {
      console.warn(`Skipping invalid post: ${postData.id}`);
      continue;
    }

    // Convert to BlogPost format
    posts.push({
      id: postData.id,
      title: postData.title,
      content: postData.content,
      date: new Date(postData.date).toISOString(), // Convert RSS date to ISO format
      imageUrl: postData.imageUrl || '/images/blog/default.jpg',
      category: postData.category || 'Uncategorized',
      author: postData.author,
      excerpt: postData.excerpt || postData.content.substring(0, 150) + '...',
    });
  }

  // Sort posts by date (newest first)
  return posts.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
};

// Helper function to safely get text content from an XML element
const getElementText = (parent: Element, tagName: string): string | null => {
  const element = parent.getElementsByTagName(tagName)[0];
  return element ? element.textContent : null;
};

