import matter from 'gray-matter';
import { parseXMLBlogPosts } from './xmlBlogService';

export interface BlogPost {
  id: string;
  title: string;
  content: string;
  date: string;
  imageUrl: string;
  category: string;
  author: string;
  excerpt: string;
}

// Import all markdown files
const blogPosts = [
  require('../content/blog/welcome.md'),
  // Add more blog posts here
];

// Function to load XML blog posts
export const loadXMLBlogPosts = async (xmlUrl: string): Promise<BlogPost[]> => {
  try {
    const response = await fetch(xmlUrl);
    const xmlString = await response.text();
    return parseXMLBlogPosts(xmlString);
  } catch (error) {
    console.error('Error loading XML blog posts:', error);
    return [];
  }
};

export const getAllPosts = (): BlogPost[] => {
  return blogPosts
    .map(post => {
      const { data, content } = matter(post.default);
      return {
        id: data.id,
        title: data.title,
        content,
        date: data.date,
        imageUrl: data.imageUrl,
        category: data.category,
        author: data.author,
        excerpt: data.excerpt,
      };
    })
    .sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
};

export const getPostById = (id: string): BlogPost | null => {
  const post = blogPosts.find(p => {
    const { data } = matter(p.default);
    return data.id === id;
  });

  if (!post) return null;

  const { data, content } = matter(post.default);
  return {
    id: data.id,
    title: data.title,
    content,
    date: data.date,
    imageUrl: data.imageUrl,
    category: data.category,
    author: data.author,
    excerpt: data.excerpt,
  };
}; 